
class Request {

  constructor(apipath) {
    let base = process.env.NODE_ENV === 'production' ? '' : "http://127.0.0.1:8888"
    this.baseUrl = base + apipath
  }

  async getAll() {
    let url = this.baseUrl
    let res = await fetch(url)
    res = await res.json();
    return res
  }

  async get(options) {
    let url = this.baseUrl
    let keys = Object.keys(options);
    let qs = "?"
    for(const item of keys) {
      qs += `${item}=${options[item]}&`
    }
    qs = qs.slice(0, -1)
    let res = await fetch(url + qs)
    res = await res.json();
    return res
  }

  async create(data) {
    let url = this.baseUrl
    let res = await fetch(url, {
      method: "post",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data)
    })
    res = await res.json();
    return res
  }

  async updateById(id, data) {
    let url = this.baseUrl + `/${id}`
    let res = await fetch(url, {
      method: "put",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data)
    })
    res = await res.json();
    return res
  }

  async deleteById(id) {
    let url = this.baseUrl + `/${id}`
    let res = await fetch(url, {
      method: "delete"
    })
    res = await res.json();
    return res
  }

}

export default Request